/* CSS Reset used: http://seodesigns.com/projects/codepen/reset.css */

.bottom-nav {
    display: block;
        height: 58px;
        box-shadow: 0 -2px 5px -2px #333;
        background-color:black;
        position: fixed;
        bottom: 0px;
        left: 0px;
        width: 100%;
        z-index: 3;
  .sticky-footer {
        display: block;
        height: 58px;
        box-shadow: 0 -2px 5px -2px #333;
        background-color:black;
        position: fixed;
        bottom: 0px;
        left: 0px;
        width: 100%;
        z-index: 3;
  }
  .sticky-footer .one-fourth {
        width: 25%;
        float: left;
        color: #fff;
        text-align: center;
        height: 58px;
        position: relative;
        cursor: pointer;
  }
  .sticky-search,
  .sticky-filters,
  .sticky-map,
  .sticky-radius {
        width: 25px;
        height: 25px;
        margin: auto;
        margin-top: 11px;
  }
    .sticky-footer .one-fourth p {
        font-size: 10px;
        margin-top: 3px;
  }
}
