
  body {
    margin: 0;
    // background: url("./svg/bg_img.png") no-repeat center center fixed;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color:slategrey
  }
  
  #root{
    background-image: linear-gradient(to top right, transparent 15%, white 300%);
  }
  
  html, body, #root, .main-container{
    display: inline-grid;
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top;
    margin: 0 0 0 0;
    justify-content: stretch;
    align-items: center;
    margin: auto;
   
    @media all and (max-width: 360px) and (max-height: 640px)  {
      display: inline-grid;
      height: 1340px;
      width: 785px;
    }

    @media all and (max-width: 411px) and (max-height: 731px)  {
      display: inline-grid;
      height: 1340px;
      width: 785px;
    }

    @media all and (max-width: 411px)  and (max-height: 823px) {
      display: inline-grid;
      height: 1513px;
      width: 786px;
    }

    @media all and (max-width: 320px)  and (max-height: 568px) {
      display: inline-grid;
      height: 1363px;
      width: 786px;
    }

    @media all and (width: 375px) {
      display: inline-grid;
        width: 786px;
      @media all and (height: 812px){
        height: 1634px;
      }
      
      @media all and (width: 667px) {
        height: 1370px;
      }
      
     
    }

    @media all and (max-width: 414px) and (max-height: 736px) {
      display: inline-grid;
      height: 1363px;
      width: 786px;
    }
   
}
