.login-container {
    justify-content: center;
    align-items: center;
  
    .logo{
        display: block;
        justify-content: space-evenly;
        align-items: center;
        margin: auto;
        width: 100px
    }

    .title{
        align-items: center;
        color: ivory;
        justify-items: center;
        display: grid;
        padding: 25px;
        font-size: xx-large;
    }

  .form {
    
    width: 400px;
    height: 230px;
    background: #edeff1;
    margin: 0px auto;
    padding-top: 20px;
    border-radius: 10px;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
  }

  input[type="text"] {
    display: block;
    width: 309px;
    height: 35px;
    margin: 15px auto;
    color: aqua;
    background: black;
    border: 0px;
    padding: 5px;
    font-size: 16px;
    border: 2px solid #fff;
    transition: all 0.3s ease;
    text-align: center;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
  }

  input[type="text"]:focus {
    border: 2px solid #1abc9d;
  }

  input[type="submit"] {
    display: block;
    background: #1abc9d;
    width: 314px;
    padding: 12px;
    cursor: pointer;
    color: #fff;
    border: 0px;
    margin: auto;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    font-size: 17px;
    transition: all 0.3s ease;
  }

  input[type="submit"]:hover {
    background: #09cca6;
  }

  a {
    text-align: center;
    font-family: Arial;
    color: gray;
    display: block;
    margin: 15px auto;
    text-decoration: none;
    transition: all 0.3s ease;
    font-size: 12px;
  }

  a:hover {
    color: #1abc9d;
  }
}
